@import '../common/global-set';
.outer{
	width:100%;
	margin-top:14px;
	border-top: solid 9px #54bfbc;
	color:black;
}
.global_center{
	width:1042px;
	margin:0 auto;
}
.vertical_line{
	margin-left:43px;
	border-left:solid 8px #54bfbc;
	border-right:solid 8px #54bfbc;
	width:82px;
	height:36px;
}
.list_text{
	
	width:164px;
	height:47px;
	line-height:47px;
	font-size:22px;
	text-align:center;
	font-weight:500;
	background-color:#54BEB2;
	color:white;
}
.headpic{
	padding-top:80px;
	color:white;
	width:240px;
	height:313px;
	border:solid 10px #497D92;
	float:left;
	margin:80px 80px 60px 25px;
	-webkit-box-shadow: 10px 10px 4px #888888;
    -moz-box-shadow: 10px 10px 4px #888888;
    box-shadow: 5px 5px 15px #888888;
}
.black{
	background-color:black;
	width:240px;
	height:313px;
	position:absolute;
	margin-top:-90px;
	margin-left:-10px;
	display:none;
	opacity:0.5;
	filter:alpha(opacity=50);
	z-index:1;
}
.name{
	margin-left:40px;
	font-size:22px;
	z-index:100;
	position:absolute;
}
.info{
	margin-left:40px;
	margin-top:70px;
	font-size:16px;
	line-height:35px;
	z-index:100;
	width:180px;
	position:absolute;
}
.tag{
	width:100%;
	height:67px;
	background-color:#54bfbc;
	text-align:center;
	clear:both;
}
.tag div{
	margin:0 auto;
	width:480px;
	height:67px;
	line-height:67px;
}
.tag a{
	font-size:22px;
	color:white;
	margin-left:60px;
	margin-right:60px;
	text-decoration:none;
	float:left;
}
.tag p{
	font-size:16px;
	color:black;
	float:left;
	font-weight:700;
}
.tag input{
	float:left;
	height:26px;
	width:25px;
	font-size:20px;
	margin:21px 17px 0 17px;
	padding-left:5px;
	line-height:25px;
}
.detial_info{
	display:none;
	position:absolute;
	width:1042px;
	margin:80px 0 60px 25px;
	height:700px;
	background-color:rgba(84,190,178,0.5);
	z-index:100;
	img{
		float: right;
		margin-top: -80px;
	}
	text-align:center;
	color:white;
	font-size:22px;
	line-height:60px;
	padding-top:80px;
}
.profile{
	display:none;
}
#content{
	height:850px;
}